@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@100;400&display=swap');

html {
  background: #3A3F4A;
}

.App {
  font-style: normal;
  font-weight: normal;
  color: #FFFFFF;
  font-size: 26px;
  line-height: 32px;
  font-family: 'Montserrat Alternates', sans-serif;
}

.page {
  position: absolute;
  top: 100px;
  left:0;
  right:0;
}

a {
    color: #FFFFFF;
    text-decoration: none;
    transition: 0.3s;
}

a:hover {
  font-size: 28px;
  transition: 0.3s;
}

.Content-Box {
  border-radius: 4px;
  position: absolute;
  display: block;
  right: 10px;
  top: 155px;
  padding: 10px;
  margin: 5px;
  width: 775px;
}

html-webkit-scrollbar {
  display: none;
}

.Text-Header {
  font-size: 72px;
  line-height: 88px;
}

.Text-Body {
  font-size: 26px;
  line-height: 32px;
}

nav {
  color: #FFFFFF;
  top: 0px;
  right: 10px;
  position: absolute;
  display: inline-block;
  text-align: center;
  padding: 8px 8px;
  text-decoration: none;
  font-size: 20px;
  line-height: 32px;
  font-family: 'Montserrat Alternates', sans-serif;
}

.Text-Button {
  padding: 10px 10px;
  margin: 2px;
  display: inline-block;
  border-style: solid;
  outline-color: #FFFFFF;
  border-width: 1px;
  border-radius: 4px;
  font-size: 20px;
  transition: 0.3s;
}

.Text-Button:hover {
  padding: 10px 10px;
  margin: 2px;
  display: inline-block;
  border-style: solid;
  outline-color: #FFFFFF;
  border-width: 1px;
  border-radius: 4px;
  font-size: 26px;
  transition: 0.3s;
}

.Particle-Box {
  position: absolute;
  left:0;
  right:0;
  width: 100%;
  height: 100%;
  z-index: -999999999999999999;
}

.Gradient-Cover {
  position: absolute;
  background: linear-gradient(to right, rgba(58,63,74,0), rgba(58,63,74,1));
  z-index: -1;
}

.move-appear,
.move-enter {
  animation-name: moveIn;
  animation-duration: 800ms;
  animation-delay: 100ms;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  opacity: 0;
  z-index:-1;
}

@keyframes moveIn {
  from {transform: translateX(-200em)}
  to {transform: translateX(0em)}
}

.move-appear-active,
.move-enter.move-enter-active {
  animation-name: moveIn;
  animation-duration: 1200ms;
  animation-delay: 100ms;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}

.move-exit {
  animation-name: fadeAway;
  animation-duration: 800ms;
  animation-delay: 100ms;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  opacity: 1;
}

@keyframes fadeAway {
  from {opacity:1}
  to {opacity:0}
}

@keyframes moveOut {
  from {transform: translateY(0em)}
  to {transform: translateY(200em)}
}

.fade-appear,
.fade-enter {
  opacity: 0;
  z-index:-1;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}

.fade-exit {
  opacity: 1;
}

